<template>
	<div class="aquaculture">
		<box-item title="水产养殖概况">
			<div class="wrapper">
				<card-item class="card" title="经营主体数" key="经营主体数" is-big>
					<div class="self-content" slot="content">
						<p>{{ operatorsNum }}<span class="unit">家</span></p>
						<p class="network-info">
							入网经营主体数：<span>{{ snOperatorsNum }}家</span>
						</p>
					</div>
				</card-item>
				<card-item class="card" title="渔业面积" key="渔业面积" is-big>
					<div class="self-content" slot="content">
						<p>{{ fisheriesFieldArea }}<span class="unit">万亩</span></p>
						<p class="network-info">
							入网渔业面积：<span>{{ fisheriesProductionLandArea }}万亩</span>
						</p>
					</div>
				</card-item>
			</div>
		</box-item>
	</div>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
import cardItem from '@/components/common/card-item';
import {areaLevelCorAreaId} from '@/common/fn';
export default {
	props: {
		farmAreaInfo: {
			type: Object,
			default() {
				return areaLevelCorAreaId();
			}
		}
	},
	data() {
		return {
			operatorsNum: 7443, // 前端写死
			snOperatorsNum: 202, // 前端写死
			fisheriesFieldArea: 18.95, // 前端写死
			fisheriesProductionLandArea: 0 // 后台获取
		};
	},
	mounted() {
		this.initMoudle();
	},
	methods: {
		initMoudle() {
			this.getFishPondArea();
		},
		getFishPondArea() {
			this.$ajax
				.post(this.$api.overview.getPlotByTypeAndArea, {
					farmAreaId: [this.farmAreaInfo.farmAreaId],
					farmAreaType: this.farmAreaInfo.farmAreaType,
					type: 3 // 渔业
				})
				.then(({data}) => {
					console.log('渔业', data);
					if (data.respCode === 0) {
						// this.fisheriesFieldArea = data.obj[0].fieldArea;
						this.fisheriesProductionLandArea = data.obj[0].productionLandArea;
					}
				})
				.catch(e => {
					console.log('渔业地块统计 error: ', e);
				});
		}
	},
	components: {BoxItem, cardItem}
};
</script>

<style lang="scss" scoped>
.aquaculture {
	height: 45%;
	box-sizing: border-box;
	margin-bottom: 15px;
	.wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		box-sizing: border-box;
	}
	.card {
		width: 76%;
		margin: 10px auto 30px;
		.unit {
			font-size: 16px;
		}
	}
	.self-content {
		text-align: center;
		font-weight: bold;
		width: 100%;
		.network-info {
			width: 100%;
			font-size: 14px;
			padding-top: 10px;
			color: rgba(0, 246, 255, 0.5);
			span {
				color: #00f6ff;
				font-size: 16px;
				font-weight: bold;
			}
		}
	}
}

@media screen and (min-width: 3000px) {
	.aquaculture {
		.card {
			width: 72%;
			.unit {
				font-size: 20px;
			}
			.self-content {
				font-size: 36px;
			}
		}
	}
}
</style>



